import React, {useState} from 'react';
import './Login.scss';
import { ReactComponent as LoadingSvg } from '../../images/loading.svg';

const Login = props => {
    const [loading, setLoading] = useState(false);

    const logginTriggered = (e) => {
        setLoading(true);
    }

    return (
        <div className="login">
            <div className="copy">
                <p>Create an awesome</p>
                <p className="pt"> Party playlist </p>
            </div>
            <a onClick={logginTriggered} href="http://localhost:8000/login">
                {loading ? <LoadingSvg/> : 
                    <span>
                        Login
                    </span>
                }
            </a>
            <div className="copy bottom">
                <p>Dance all night long</p>
            </div>
        </div>
    )

};

export default Login;
